.modal {
  color: #fff;
  align-items: center;
  bottom: 0;
  display: none;
  justify-content: center;
  left: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0.4rem;
  position: fixed;
  right: 0;
  top: 0;
}
.modal:target,
.modal.active {
  display: flex;
  opacity: 1;
  z-index: 400;
}
.modal:target .modal-overlay,
.modal.active .modal-overlay {
  background: rgba(25, 28, 34, 0.95);
  bottom: 0;
  cursor: default;
  display: block;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
.modal:target .modal-container,
.modal.active .modal-container {
  animation: slide-down 0.2s ease 1;
  z-index: 1;
  -webkit-animation: slide-down 0.2s ease 1;
}
.modal.modal-sm .modal-container {
  max-width: 320px;
  padding: 0 0.4rem;
}
.modal.modal-lg .modal-container {
  box-shadow: none;
  max-width: 960px;
}

@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(-1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-container {
  /* background: #191C22; */
  /* border: .1rem solid rgba(34, 136, 255, 0.8); */
  box-sizing: content-box;
  border-radius: 0.1rem;
  display: flex;
  flex-direction: column;
  max-height: 85vh;
  max-width: 640px;
  padding: 0 0.8rem;
  text-align: left;
  width: 100%;
}
.modal-container:hover {
  border: 0.1rem solid #28f;
}
.modal-container.modal-fullheight {
  max-height: 100vh;
}
.modal-container .modal-header {
  color: #cde;
  padding: 0.8rem;
}
.modal-container .modal-body {
  max-height: 65vh;
  overflow-y: auto;
  padding: 0.8rem;
  position: relative;
}
.modal-container .modal-footer {
  padding: 0.8rem;
  text-align: right;
}

/* Modal Button Clear */
.btn.btn-clear {
  float: right;
  font-size: 1.5rem;
  text-align: center;
  border-radius: 0.1rem;
  background: transparent;
  border: 0;
  color: currentColor;
  height: 1.4rem;
  line-height: 1.2rem;
  margin-left: 0.2rem;
  margin-right: -2px;
  opacity: 1;
  padding: 0;
  text-decoration: none;
  width: 1.4rem;
}
.btn.btn-clear:hover {
  opacity: .95;
}
.btn.btn-clear:focus {
  box-shadow: 0 0 0 0.1rem rgba(34, 136, 255, 0.2);
}
.btn.btn-clear::before {
  color: #28f;
  content: "\2715";
}

@media(max-width: 600px) {
  .modal-container:hover {
    border: 0;
  }
}
